import { Injectable } from '@angular/core';
import { AngularFirestoreCollection, AngularFirestore } from 'angularfire2/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';
import { FoodContent } from 'src/Table/table';
import { AppError } from '../common/app-error';
import { BadInput } from '../common/bad-input';
import { NotFoundError } from '../common/not-found-error';

@Injectable({
  providedIn: 'root'
})
export class FoodContentService {
  private foodContentCollectionList: AngularFirestoreCollection<any>;
  private foodContentList: Observable<any[]>;
  constructor(private db: AngularFirestore) {
    this.foodContentCollectionList = db.collection<FoodContent>('FoodContent');
  }
  create(foodContentObj) { 
    return this.foodContentCollectionList.add(foodContentObj)
  }
  getAllFoodContent() {
    this.foodContentList = this.foodContentCollectionList.snapshotChanges().pipe(
      map(actions => {
        return actions.map(a => {
          const data = a.payload.doc.data();
          const id = a.payload.doc.id;
          return { id, ...data };
        });
      })
    );
    return this.foodContentList;
  }
  getFoodContent(id) {
    return this.foodContentCollectionList.doc<any>(id).valueChanges();
  }
  updateFoodContent(foodContentObj: FoodContent, id: string) {
    return this.foodContentCollectionList.doc(id).update(foodContentObj)
    .catch(this.handleError);
  }
  removeFoodContent(id) {
    return this.foodContentCollectionList.doc(id).delete()
    .catch(this.handleError);
  }
  private handleError(error: Response) {
    if (error.status === 400)
      return Observable.throw(new BadInput(error.json()));
  
    if (error.status === 404)
      return Observable.throw(new NotFoundError());
    
    return Observable.throw(new AppError(error));
  }
}
